const Beaufort = [
    {
        windFrom: 0,
        windTo: 2,
        force: 0
    },
    {
        windFrom: 2,
        windTo: 6,
        force: 1
    },
    {
        windFrom: 6,
        windTo: 12,
        force: 2
    },
    {
        windFrom: 12,
        windTo: 20,
        force: 3
    },
    {
        windFrom: 20,
        windTo: 29,
        force: 4
    },
    {
        windFrom: 29,
        windTo: 39,
        force: 5
    },
    {
        windFrom: 39,
        windTo: 50,
        force: 6
    },
    {
        windFrom: 50,
        windTo: 62,
        force: 7
    },
    {
        windFrom: 62,
        windTo: 75,
        force: 8
    },
    {
        windFrom: 75,
        windTo: 89,
        force: 9
    },
    {
        windFrom: 89,
        windTo: 103,
        force: 10
    },
    {
        windFrom: 103,
        windTo: 118,
        force: 11
    },
    {
        windFrom: 118,
        windTo: 10000,
        force: 12
    }
]

document.addEventListener('DOMContentLoaded', () => {

    window.scroll({
        left: 0,
        top: 0,
    })

    const sun = document.querySelector('#bg-sun')
    const search = document.querySelector('#search')
    const searchButton = document.querySelector('#searchButton')
    const day1Element = document.querySelector('#day1')
    const day2Element = document.querySelector('#day2')
    const day3Element = document.querySelector('#day3')
    const cityLink = document.querySelector('#city')
    const backButton = document.querySelector('#back')
    const loader = document.querySelector('#loader')

    const gpsBox = document.querySelector('#gps-box')
    const gpsButton = document.querySelector('#gps')

    let loading = false

    const iconsDay1 = document.querySelector('#icons-day-1')
    const iconsDay2 = document.querySelector('#icons-day-2')
    const iconsDay3 = document.querySelector('#icons-day-3')

    const windDay1 = document.querySelector('#wind-day-1')
    const windDay2 = document.querySelector('#wind-day-2')
    const windDay3 = document.querySelector('#wind-day-3')

    const weatherIcons = {
        "sun": "005-sun.svg",
        "thunder": "001-thunder.svg",
        "rain": "002-rain.svg",
        "cloud2": "003-cloudy.svg",
        "cloudy": "004-cloud2.svg",
        "cloud": "005-clouds.svg",
    }

    let previousScrollY = 0

    const sunBehavior = _.throttle((y, max) => {
        const pos = 1 - (y / max)
        const sunPos = y * (pos + 0.5)
        sun.style.transform = `translateY(${sunPos}px)`
    }, 30)
        
    window.addEventListener('scroll', _.debounce(e => {
        const y = e.pageY
        const max = window.innerHeight
        
        //Auto scroll
        if(y - previousScrollY > 0) {
            window.scroll({
                left: 0,
                top: max,
                behavior: 'smooth'
            })
        } else {
            window.scroll({
                left: 0,
                top: 0,
                behavior: 'smooth'
            })
        }

        sunBehavior(y, max)

        previousScrollY = y
    }, 30))

    const weatherFromCity = city => {
        return fetch(`https://api.openweathermap.org/data/2.5/forecast?q=${city}&APPID=6841e5450643e5d4ff59981dbf58944e`)
        .then(response => {
            return response.json();
        })
    }

    const weatherFromCoord = (lat, long) => {
        return fetch(`https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${long}&APPID=6841e5450643e5d4ff59981dbf58944e`)
        .then(response => {
            return response.json();
        })
    }

    const getWeather = dataPromise => {
        loader.classList.remove('hide')

        return dataPromise
        .then(data => {

            const weatherId = id => {
                if(id === 800) {
                    return "sun"
                } else if(id === 801) {
                    return "cloudy"
                } else if(id < 900 && id >= 802) {
                    return "cloud1"
                } else if((id < 800 && id >= 700) || (id < 700 && id >= 600)) {
                    return "cloud2"
                } else if(id >= 900 || id < 300) {
                    return "thunder"
                } else if((id < 400 && id >= 300) || (id < 600 && id >= 500)) {
                    return "rain"
                }
            }

            const collectData = day => {
                const wind = day.wind.speed
                
                const windForce = Beaufort.find(e => (e.windFrom <= wind && wind <= e.windTo))
                return {
                    date: moment(day.dt*1000).format('dddd DD MMM'),
                    temp: Math.round(day.main.temp - 273.15, 2),
                    humidity: day.main.humidity,
                    wind: wind,
                    beaufort: windForce.force,
                    weather: weatherId(day.weather[0].id)
                }
            }

            const day1 = collectData(data.list[0])
            windDay1.className = `wind icon b-${day1.beaufort}`
            const day2 = collectData(data.list[8])
            windDay2.className = `wind icon b-${day2.beaufort}`
            const day3 = collectData(data.list[16])
            windDay3.className = `wind icon b-${day3.beaufort}`

            const city = {
                name: data.city.name,
                lat: data.city.coord.lat,
                long: data.city.coord.lon,
                country: data.city.country
            }

            cityLink.text = `${city.name}, ${city.country}`
            cityLink.href = `https://www.google.com/maps/place/${city.lat},${city.long}`

            return {day1, day2, day3}
        })
        .then(days => {
            const changeIcons = (iconsParent, weatherId) => {
                iconsParent.childNodes.forEach(node => {
                    if(node.className.indexOf(weatherId) != -1) {
                        if(node.classList.contains('hide'))
                            node.classList.remove("hide")
                    } else {
                        if(!node.classList.contains('hide'))
                            node.classList.add("hide")
                    }
                })
            }

            const updateElement = (day, element) => {
                element.querySelector('.date').textContent = day.date
                element.querySelector('.temp').textContent = day.temp
                element.querySelector('.humidity').textContent = day.humidity
                element.querySelector('.wind').textContent = day.wind
                // element.querySelector('.sunset').textContent = weatherIcons[day.weather]
            }
            
            updateElement(days.day1, day1Element)
            updateElement(days.day2, day2Element)
            updateElement(days.day3, day3Element)

            //Change weather svg
            changeIcons(iconsDay1, days.day1.weather)
            changeIcons(iconsDay2, days.day2.weather)
            changeIcons(iconsDay3, days.day3.weather)

            loader.classList.add('hide')
        })
    }

    // search.addEventListener('change', e => {
    //     console.log("Change");
        
    //     getWeather(e.target.value)
    //     .then(() => {
    //         window.scroll(0, window.innerHeight)
    //     })
    //     .catch(e => {
    //         console.error("Weather", e)
    //     })
    // })

    const request = city => {
        getWeather(weatherFromCity(city))
        .then(() => {
            window.scroll({
                left: 0,
                top: window.innerHeight,
                behavior: "smooth"
            })
        })
        .catch(e => {
            console.error("Weather", e)
            search.parentElement.classList.add('shake')
            setTimeout(() => {
                search.parentElement.classList.remove('shake')
            }, 1000)
            loader.classList.add('hide')
        })
    }

    searchButton.addEventListener('click', e => {
        request(search.target.value)
    })

    search.addEventListener('keydown', e => {
        if(e.keyCode == 13) {
            request(e.target.value)
        }
    })

    backButton.addEventListener('click', e => {
        search.value = ""
        search.focus()
    })

    gpsButton.addEventListener('click', e => {
        navigator.geolocation.getCurrentPosition(position => {
            getWeather(weatherFromCoord(position.coords.latitude, position.coords.longitude))
            .then(() => {
                window.scroll({
                    left: 0,
                    top: window.innerHeight,
                    behavior: "smooth"
                })
            })
            .catch(e => {
                gpsBox.classList.add('shake')
                setTimeout(() => {
                    gpsBox.classList.remove('shake')
                }, 1000)
                loader.classList.add('hide')
            })
        }, err => {
            console.error('Geolocation Error', err)
            gpsBox.classList.add('shake')
                setTimeout(() => {
                    gpsBox.classList.remove('shake')
                }, 1000)
        })
    })

    if ("geolocation" in navigator) {
        /* geolocation is available */
        gpsBox.classList.remove('hide')
    }

    getWeather(weatherFromCity('Paris'))

}, false)